.end-page-endButton, .end-page-restartButton{
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 30% 0 30%;
  display: inline-block;
  padding: 2px 0% 2px 0%;
  background-color: #488be2;
  border-radius: 20px 20px 20px 20px;
}

.end-page-endButton {
  top: 60%;
}
.end-page-restartButton {
  top: 75%;
}

.end-page-endButton:hover, .end-page-restartButton:hover {
  cursor: pointer;
}
.end-page-bg {
  height: 100%;
  width: 100%;
}
