.question-img {
  height: 25vh;
  width: auto;
  border: 1px solid rgba(30,30,30,0.20);
}
.game-page-body {
  margin: 0;
  height: 90%;
  top: 8vh;
  left: 0;
  position: absolute;
    background-color: lightblue;
}
.word-list-table {
  position: relative;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: lightblue;
}

.submit-btn {
  background-color: #488be2;
  font-size: 50px;
  display: block;
  height: 100%;
  line-height: 100px;
}

.game-page-top td {
  font-size: 3vh;
  height: auto;
  vertical-align: top;
}

.game-page-top {
  height: 40%;
}


.game-page-btm{
  vertical-align: top;
}

.submit-btn:hover {
  cursor: pointer;
}

.nextQuestionButton {
  position: relative;
  width: 50%;
  left: 0;
  right: 0;
  margin: 0 10% 0 10%;
  top: 20vh;
  display: inline-block;
  padding: 2px 0% 2px 0%;
  background-color: #488be2;
  border-radius: 10px 10px 10px 10px;
}

.nextQuestionButton:hover {
  cursor: pointer;
}

.leftElm {
  float:left;
  /* font-size: 20px; */
  text-align: left;
  padding-left: 10px;
  display: inline-flex;
}


.rightElm {
  float:right;
  /* font-size: 20px; */
  text-align: right;
  padding-right: 10px;
  display: inline-flex;
}

.gameStat {
  display: inline;
}

.nq-btn{

}

.question{
  
  
  width: 6vh;
  height: 6vh;
  margin-left: 0.5vh;
  margin-right: 0.5vh;
  text-align: center;
  vertical-align: top;
  float: center;
  display:inline-block;

}

.question_highlighted{
  
    border:3px solid red;
    width: 6vh;
    height: 6vh;
    margin-left: 0.5vh;
    margin-right: 0.5vh;
    text-align: center;
    vertical-align: top;
    float: center;
    display:inline-block;
 
}
.questionTable tr td{
  border-bottom: 1px solid black;
  height: 7vh;
  width: 100vw;
  font-size: 4vh;
  padding: 0 0 0 0;
  vertical-align: middle;

}

.questionTable {
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  
  border-spacing: 0px;
}