@keyframes redBorder {
    0% {
      border-color: black;
      background-color: #DDDDDD;
    }
    50% {
      border-color: red;
      background-color: red;
    }
    100% {
      border-color: black;
      background-color: #DDDDDD;
    }
}

.wrong-click{
  animation-name: redBorder;
  animation-duration: 0.7s;
  animation-play-state: running;
}

.wordcard-container {
  background-color: #DDDDDD;
  font-size: 4vh;
  max-height: 100px;
  vertical-align: middle;
  border: 1px solid black;
}
.wordcard-container:hover {
  cursor: pointer;
}
