body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  height: 100%;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
}

@media only screen and (min-width: 700px) {

  body {
  
       width:600px;
    }

}


#root {
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 10px 0px 10px 0px;
}
#page {
  width: 100%;
  height: 100%;
  border-spacing: 0px;
}

tbody {
  /* vertical-align: top; */
  height: 100%;
    background-color:lightblue;
}

thead {
    
  font-size: 3vh;
  background-color: #488be2;
  color:white;

}



.w3-container:after, .w3-container:before {
  height: 0;
  width: 0;
      padding: 5px;
}

.title-bar{
  text-align: center;
}

.start-page-btm {
  height: 100%;
}

.start-page-startButton {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 50%;
  top: 50%;
  color: white;
  display: inline-block;
  padding: 2px 0% 2px 0%;
  background-color: #488be2;
  border-radius: 20px 20px 20px 20px;
}

.start-page-startButton:hover {
  cursor: pointer;
   
}
.start-page-bg {
  height: 100%;
  width: 100%;
}

.start-page-welcomePhrase {
  margin-top: 20vh;
}

.wordsublist{
  width: 100%;

}

.wordsublist td{
  vertical-align: middle;
  padding: 5px 10px 5px 10px;
}

@-webkit-keyframes redBorder {
    0% {
      border-color: black;
      background-color: #DDDDDD;
    }
    50% {
      border-color: red;
      background-color: red;
    }
    100% {
      border-color: black;
      background-color: #DDDDDD;
    }
}

@keyframes redBorder {
    0% {
      border-color: black;
      background-color: #DDDDDD;
    }
    50% {
      border-color: red;
      background-color: red;
    }
    100% {
      border-color: black;
      background-color: #DDDDDD;
    }
}

.wrong-click{
  -webkit-animation-name: redBorder;
          animation-name: redBorder;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.wordcard-container {
  background-color: #DDDDDD;
  font-size: 4vh;
  max-height: 100px;
  vertical-align: middle;
  border: 1px solid black;
}
.wordcard-container:hover {
  cursor: pointer;
}

.ans-cell{
  border:1px solid #000;
  width: 6vh;
  height: 6vh;
  margin-left: 0.5vh;
  margin-right: 0.5vh;
  text-align: center;
  vertical-align: top;
  float: center;
  display:inline-block;
}

.ans-2cell {
  border:1px solid #000;
  width: 10vh;
  height: 6vh;
  margin-left: 0.5vh;
  margin-right: 0.5vh;
  text-align: center;
  vertical-align: top;
  float: center;
  display:inline-block;
}

.ans-spacecell {
  
  width: 6vh;
  height: 6vh;
  margin-left: 0.5vh;
  margin-right: 0.5vh;
  text-align: center;
  vertical-align: top;
  float: center;
  display:inline-block;
}

.answerFieldTable tr td{
  border-bottom: 1px solid black;
  height: 7vh;
  width: 100vw;
  font-size: 4vh;
  padding: 0 0 0 0;
  vertical-align: middle;

}

.answerFieldTable {
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: lightgreen;
  border-spacing: 0px;
}

.tick{
  stroke-dasharray: 50;
  stroke-dashoffset: 50;
  transition: stroke-dashoffset 1s  ease-out;
}

 #tick{
   -webkit-transform-origin: 50% 0px;
           transform-origin: 50% 0px;
   -webkit-transform: scale(0.4) translateY(-10vh);
           transform: scale(0.4) translateY(-10vh);
   display: inline-flex;
   position: absolute;
   left: 0;
   right: 0;

 }

.drawn + svg .path{
  opacity: 1;
  stroke-dashoffset: 0;
}

.tick-wrapper .cross-wrapper{
  position: relative;
  height: 0;
}


@-webkit-keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}


@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

.question-img {
  height: 25vh;
  width: auto;
  border: 1px solid rgba(30,30,30,0.20);
}
.game-page-body {
  margin: 0;
  height: 90%;
  top: 8vh;
  left: 0;
  position: absolute;
    background-color: lightblue;
}
.word-list-table {
  position: relative;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: lightblue;
}

.submit-btn {
  background-color: #488be2;
  font-size: 50px;
  display: block;
  height: 100%;
  line-height: 100px;
}

.game-page-top td {
