.ans-cell{
  border:1px solid #000;
  width: 6vh;
  height: 6vh;
  margin-left: 0.5vh;
  margin-right: 0.5vh;
  text-align: center;
  vertical-align: top;
  float: center;
  display:inline-block;
}

.ans-2cell {
  border:1px solid #000;
  width: 10vh;
  height: 6vh;
  margin-left: 0.5vh;
  margin-right: 0.5vh;
  text-align: center;
  vertical-align: top;
  float: center;
  display:inline-block;
}

.ans-spacecell {
  
  width: 6vh;
  height: 6vh;
  margin-left: 0.5vh;
  margin-right: 0.5vh;
  text-align: center;
  vertical-align: top;
  float: center;
  display:inline-block;
}

.answerFieldTable tr td{
  border-bottom: 1px solid black;
  height: 7vh;
  width: 100vw;
  font-size: 4vh;
  padding: 0 0 0 0;
  vertical-align: middle;

}

.answerFieldTable {
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: lightgreen;
  border-spacing: 0px;
}

.tick{
  stroke-dasharray: 50;
  stroke-dashoffset: 50;
  -webkit-transition: stroke-dashoffset 1s  ease-out;
  -moz-transition: stroke-dashoffset 1s  ease-out;
  -ms-transition: stroke-dashoffset 1s ease-out;
  -o-transition: stroke-dashoffset 1s  ease-out;
  transition: stroke-dashoffset 1s  ease-out;
}

 #tick{
   transform-origin: 50% 0px;
   transform: scale(0.4) translateY(-10vh);
   display: inline-flex;
   position: absolute;
   left: 0;
   right: 0;

 }

.drawn + svg .path{
  opacity: 1;
  stroke-dashoffset: 0;
}

.tick-wrapper .cross-wrapper{
  position: relative;
  height: 0;
}


@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}
